 .jctkr-wrapper {
  box-sizing: border-box;

  /* margin-top: -2px; */

  * {
    box-sizing: border-box;

    /* margin-top: -2px; */
  }

  display: inline-block;
  position: relative;
  width: 100%;
  height: 78px;
  vertical-align: top;
  overflow: hidden;

  /* line-height: 57px; */
  /* padding: 20px 0; */

  ul {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
    white-space: nowrap;
    font-size: 0;
    opacity: 0;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
  }

  &.jctkr-initialized ul {
    opacity: 1;
  }

  ul li {
    display: inline-block;
    font-family: sans-serif;
    font-size: 16px;
  }
}

.jctkr-label {
  display: inline-block;
}